.ava_name_wrapper a {
    text-decoration: none;
}
.active {
    color: gold;
    font-style: italic;
}
.ava_wrapper img {
    width: 50px;
    border-radius: 50%;
    display: inline-block;
    margin-bottom: 2px;
}
.ava_wrapper img:hover {
    box-shadow: 2px 4px 5px #000;
}
.ava_name_wrapper a:hover {
    color: gold;
    font-style: italic;
}
.ava_name_wrapper a:hover .ava_wrapper img {
    box-shadow: 2px 4px 5px #000;
}
.ava_name_wrapper {
    padding-bottom: 15px;
    text-align: center;
    width: fit-content;
    margin-right: auto;
    margin-left: auto;
}