
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: none;
}

html {
    height: 100%;
}
.App_wrapper {
  overflow: hidden;
  min-height: 100vh;
  display: grid;
  grid-template: "Header Header"
          "Leftsidebar App_wrapper_content"
          "Footer Footer";
  grid-gap: 10px;
    grid-template-columns: auto 1fr;
    grid-template-rows: auto 1fr auto;
}
.App_wrapper_content {
    grid-area: App_wrapper_content;
    background-color: #efede8;
    padding: 10px;
}

