.Leftsidebar {
  grid-area: Leftsidebar;  
  background-color: lightblue;
  padding: 10px;
    width: 126px;
}
.Leftsidebar ul {
	list-style-type: none;
	padding-inline-start: 0px;
}
.active {
    color: gold;
    font-style: italic;
}
