.ava_wrapper {
    width: 20px;
    height: 20px;
    background-color: #c7c785;
    border-radius: 50%;
    margin-right: auto;
    margin-left: auto;
}
.ava_wrapper:hover {
    display: block;
    box-shadow: 2px 4px 5px #000;
    cursor: pointer;
}
.name_wrapper {
    font-size: 9px;
    text-align: center;
    width: 2.5rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
.name_wrapper:hover {
    color: gold;
    font-style: italic;
    cursor: pointer;
}
.one_friend_container:hover {
    cursor: pointer;
}
.one_friend_container:hover .name_wrapper p{
    color: gold;
    font-style: italic;
}
.one_friend_container:hover .ava_wrapper{
    box-shadow: 2px 4px 5px #000;
}
