.modal {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.login_page {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, .9);
    border-radius: .3rem;
    outline: 0;
    width: 250px;
    height: 300px;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.05);
    padding: 1rem;
}
.login_page_error {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, .9);
    border-radius: .3rem;
    outline: 0;
    width: 250px;
    height: 300px;
    align-items: center;
    justify-content: center;
    background: #c8b5b5;
    padding: 1rem;
}
.modal form {
    padding: 1rem;
    width: 250px;
}
.button {
    width: 70px;
    margin: 10px 0 0 0;
    border: 2px solid;
    padding: 3px;
    box-shadow: 2px 4px 5px #000;
    border-radius: 5px;
    cursor: pointer;
}

.textarea_wrapper {
    position: relative;
    justify-content: center;
    display: flex;
    margin-bottom: 1rem;
}
.textarea_wrapper input {
    padding: 3px;
    border-radius: 0.3rem;
}
.buttons_wrapper {
    display: flex;
    justify-content: space-evenly;
}
.spans_wrapper {
    background-color: red;
    color: white;
    font-size: 0.8rem;
    border-radius: 2px;
    position: absolute;
    left: 205px;
    top: 3px;
}
.textarea_wrapper .spans_wrapper .inner_span {
    position: absolute;
    border: 6px solid transparent;
    border-right-color: red;
    background-color: transparent;
    left: -12px;
    top: 13%;
}
.captcha_wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.captcha_wrapper img {
    width: 35%;
}
.captcha_wrapper input {
    width: 35%;
    padding: 3px;
    border-radius: 0.3rem;
}
.spans_wrapper_captcha {
    background-color: red;
    color: white;
    font-size: 0.8rem;
    border-radius: 2px;
    position: absolute;
    left: 185px;
    top: 192px;
}
.inner_span_captcha {
    position: absolute;
    border: 6px solid transparent;
    border-top-color: red;
    background-color: transparent;
    left: 20px;
    top: 102%;
}
.initial_error_wrapper {
    color: red;
    position: relative;
    justify-content: center;
    display: flex;
    margin-bottom: 1rem;
}