.ProfileInfo {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 1px;
    padding: 10px;
}
.formWrapper {
    margin-left: 3rem;
}
.avatar_img img:hover {
    cursor: pointer;
}
.avatar_img img {
  width: 150px;
    border-radius: 50%;
}
.ProfileDescription {
    margin-left: 3rem;
}
.rowWrapper {
    display: flex;
    justify-content: space-between;
    width: 27%;
    margin-bottom: 0.5rem;
}
.fieldWithCheckboxWrapper {
    width: 27%;
    margin-bottom: 0.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.editModeButton {
    width: 70px;
    margin: 10px 0 30px 3rem;
    border: 2px solid;
    padding: 3px;
    box-shadow: 2px 4px 5px #000;
    border-radius: 5px;
    cursor: pointer;
}
.editModeButton:active{
    top: .1em;
    left: .1em;
    box-shadow: 0 0 0 0 rgba(0,0,0,0) inset;
    background: linear-gradient(45deg, #EECFBA, #C5DDE8);
}
.input_feedback {
    font-size: 0.8rem;
    color: red;
    width: 27%;
    text-align: end;
    margin-bottom: 0.5rem;
}