.Profile {
    display: grid;
    grid-template:
            "top_img" auto
            "ProfileInfo" auto
            "Posts" auto
          / auto;
}
.top_img {
    grid-area: top_img;
    height: 200px;
    background: url("./phonepicutre.jpg") no-repeat;
    background-size: 100%, 50%;
}
.ProfileInfo {
    grid-area: ProfileInfo;
}
.Posts {
    grid-area: Posts;
}
.input {
    margin-bottom: 1rem;
}
.input:hover {
    cursor: pointer;
}
