
.My_posts {
 grid-area: My_posts;
}
.My_posts button {
 width: 70px;
 margin: 10px 0 30px 0;
 border: 2px solid;
 padding: 3px;
 box-shadow: 2px 4px 5px #000;
 border-radius: 5px;
 cursor: pointer;
}

.My_posts button:active{
 top: .1em;
 left: .1em;
 box-shadow: 0 0 0 0 rgba(0,0,0,0) inset;
 background: linear-gradient(45deg, #EECFBA, #C5DDE8);
}
.textarea_wrapper {
 position: relative;
}
.textarea_wrapper textarea {
 resize: none;
}
.spans_wrapper {
 background-color: red;
 color: white;
 font-size: 0.8rem;
 border-radius: 2px;
 position: absolute;
 left: 100px;
 top: -22px;
}
.textarea_wrapper .spans_wrapper .inner_span {
 position: absolute;
 border: 6px solid transparent;
 border-top-color: red;
 background-color: transparent;
 bottom: -12px;
 left: 45%;
}
