.center_wrapper {
    padding-right: 25px;
}
.center_wrapper textarea {
    width: 100%;
    height: 100px;
    resize: none;
}
.center_wrapper button {
    width: 70px;
    margin: 10px 0 30px 0;
    border: 2px solid;
    padding: 3px;
    box-shadow: 2px 4px 5px #000;
    border-radius: 5px;
    cursor: pointer;
}
.center_wrapper button:active{
    top: .1em;
    left: .1em;
    box-shadow: 0 0 0 0 rgba(0,0,0,0) inset;
    background: linear-gradient(45deg, #EECFBA, #C5DDE8);
}
.textarea_wrapper textarea {
    resize: none;
}
.textarea_wrapper {
    position: relative;
}
.spans_wrapper {
    background-color: red;
    color: white;
    font-size: 0.8rem;
    border-radius: 2px;
    position: absolute;
    top: -40px;
}
.textarea_wrapper .spans_wrapper .inner_span {
    position: absolute;
    border: 6px solid transparent;
    border-top-color: red;
    background-color: transparent;
    bottom: -12px;
    left: 45%;
}