.Right_side_container {
    display: grid;
    grid-template-columns: 6fr 6fr;
    grid-column-gap: 25px;
}

.face_chat_wrapper {
    display: grid;
    grid-template: "face_grid_wrapper triangle chat"
                   "chat_name triangle chat";
    grid-template-columns: 1fr 1fr 10fr;
    margin-bottom: 20px;
}
.chat {
    grid-area: chat;
    border: 1px solid #c7c785;
    border-radius: 4px;
    padding: 10px;
}
.face_grid_wrapper {
    grid-area: face_grid_wrapper;
    margin-bottom: 0px;
    margin-top: auto;
}
.face_wrapper {
    width: 50px;
    height: 50px;
    margin: auto;
    background-color: #c7c785;
    border-radius: 50%;
}
.chat_name {
    grid-area: chat_name;
    text-align: center;
}
.triangle {
    grid-area: triangle;
    margin-top: auto;
    margin-bottom: auto;
}