.Header {
    grid-area: Header;
    background-color: lightyellow;
    padding-left: 10px;
    padding-top: 10px;
    display: grid;
    grid-template-columns: 11fr 1fr;
}

.Header img {
    width: 30px;
}

.Header svg {
    width: 20px;
    height: 20px;
}

.auth_container a {
    color: black;
    text-decoration: none;
}
.logout_wrapper,
.login_wrapper {
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.login_wrapper:hover,
.login_wrapper:hover a {
    background-color: gold;
    color: white;
    cursor: pointer;
}
.span_logout {
    font: italic 0.8rem courier;
}
.logout_wrapper:hover {
    background-color: gold;
    color: white;
    fill: white;
    cursor: pointer;
}